@import '@/assets/css/smart-grid'
$m: 1.5rem

.case-wrapper
	min-height: 100vh
	padding-bottom: 3rem
.case-title
	padding-top: $m
.case-block
	margin-top: $m * 2
.case-text
	font-size: 1.25rem
.case-img
	margin-top: $m
.screens
	+row-flex()
	justify-content: space-around
	.case-screen
		+col()
		+size(6)
		+size-lg(12)
		margin-top: $m
		cursor: pointer
		transition: .3s all
		&:hover
			transform: scale(1.025)

@media screen and (max-width: $break_xs)
	$m: .5rem

	.block
		margin-top: $m * 2
	.case-text
		font-size: 1rem
	.case-img
		margin-top: $m
	.screens
		.case-screen
			margin-top: 0
			&:not(:first-child)
				margin-top: $m * 2
</style>
